:root {
  --textcolor: #3d4e61;
  --white: #e1e7ed;
  --horizontal-margin: 8px;
}

@font-face {
  font-family: Avenir, sans-serif;
  src: url("Avenir-Book.657c3568.ttf");
}

.page__body {
  box-sizing: border-box;
  margin: 0;
  font-weight: 300;
}

.form {
  border: 1px var(--white) solid;
  border-radius: 4px;
  flex-shrink: 0;
  align-items: center;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.big-form {
  margin: 20px var(--horizontal-margin);
  height: 68px;
}

.big-form:after {
  content: " ";
  left: calc(var(--horizontal-margin)  + 26px);
  background: url("Search.ccd3c07b.svg");
  width: 19px;
  height: 19px;
  position: absolute;
}

.big-form:hover {
  box-shadow: 0 4px 4px #00000040;
}

.small-form {
  margin: 0 var(--horizontal-margin);
  height: 40px;
}

.small-form:after {
  content: " ";
  left: calc(var(--horizontal-margin)  + 13px);
  background: url("Search.ccd3c07b.svg") 0 0 / contain no-repeat;
  flex-shrink: 0;
  width: 11px;
  height: 11px;
  position: absolute;
}

.small-form:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.form__input {
  border: none;
  border-radius: 4px;
  outline: none;
  width: 100%;
}

.form__input:focus {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
}

.form__input::placeholder {
  color: var(--textcolor);
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.form__input::placeholder:focus {
  box-shadow: 0 4px 4px #00000040;
}

.big-form__input {
  padding: 24px 0 25px 62px;
  font-size: 16px;
}

.small-form__input {
  padding: 11px 0 10px 33px;
  font-size: 14px;
}

